import React, { Component } from 'react';
import '../stylesheets/App.css';
import Art from './views/Art';
import Blog from './views/Blog';
import Home from './views/Home';
import Music from './views/Music';
import Photography from './views/Photography';

import { Nav } from 'react-bootstrap';
import { 
  BrowserRouter as Router, 
  Route, 
} from 'react-router-dom';

class App extends Component {
  render() {
    return (
      <Router>
        <div className="App">

          <h1>WELCOME TO MEPHOBIA</h1>

          <Nav variant="pills" defaultActiveKey="/mephobia">
            <Nav.Item><Nav.Link to="/mephobia">Home</Nav.Link></Nav.Item>
              <Nav.Item><Nav.Link to="/mephobia/blog">Blog</Nav.Link></Nav.Item>
              <Nav.Item><Nav.Link to="/mephobia/art">Art</Nav.Link></Nav.Item>
              <Nav.Item><Nav.Link to="/mephobia/photography">Photography</Nav.Link></Nav.Item>
              <Nav.Item><Nav.Link to="/mephobia/music">Music</Nav.Link></Nav.Item>
          </Nav>

          <Route path="/mephobia" exact={true} component={Home}/>
          <Route path="/mephobia/blog" exact={true} component={Blog}/>
          <Route path="/mephobia/art" exact={true} component={Art}/>
          <Route path="/mephobia/photography" exact={true} component={Photography}/>
          <Route path="/mephobia/music" exact={true} component={Music}/>


        </div>
      </Router>
    );
  }
}

export default App;
